<template>
  <Card title="">
    <div class="switch">
      <VSwitch
        :checked="settings.isDarkTheme"
        @change="onChange('isDarkTheme', $event)"
      >
        
      </VSwitch>
      <VSwitch
        :checked="settings.isFullMode"
        @change="onChange('isFullMode', $event)"
      >
        
      </VSwitch>
      <div style="text-align: center">
      <Button sm dropdown @click="isOpenFullMode = !isOpenFullMode"></Button>
      </div>
      <div class="desc" v-show="isOpenFullMode">
        <p>
          DIY
          
          ON
          
        </p>
        <img
          src="../assets/doc_fullmode.png"
          style="width: 100%; margin-bottom: 1rem"
        />
        <p>
          <span style="font-weight: bold"
            ></span
          >
        </p>
      </div>
    </div>
  </Card>
</template>

<script>
import Card from "../components/Card";
import VSwitch from "../components/VSwitch";
import Button from "../components/Button";

export default {
  components: {
    Card,
    VSwitch,
    Button,
  },
  props: {
    settings: Object,
  },
  data() {
    return {
      isOpenFullMode: false
    }
  },
  methods: {
    onChange(key, value) {
      const obj = {};
      obj[key] = value;
      const newSetting = Object.assign(this.settings, obj);
      this.$emit("change", newSetting);
    },
  },
};
</script>

<style lang="scss" scoped>
.desc {
  margin-top: 1rem;
  padding-left: 1rem;
  font-size: 12px;
}
</style>
